import React, { createContext, useContext, useState } from 'react';
import { NavLink, Outlet, useNavigate } from 'react-router-dom';
import { Button } from '../../shared/ui/button';
import { cn } from '../../shared/ui/utils';
import { useThemeMode } from '../theme-context';
import { useI18n } from '../i18n-context';
import { useAuthStore } from '../../shared/auth/store';
import { ChevronLeft, ChevronRight } from 'lucide-react';

// Context  truyn sidebar state xung children
const SidebarContext = createContext<{ collapsed: boolean }>({ collapsed: false });
export const useSidebar = () => useContext(SidebarContext);

const navItems = [
  { to: '/app/dashboard', label: 'Dashboard', description: 'Widgets & KPIs' },
  { to: '/app/alerts', label: 'Alerts', description: 'Trung tm cnh bo' },
  { to: '/app/preferences', label: 'Preferences', description: 'Giao din & b cc' },
];

const MainLayout: React.FC = () => {
  const { mode, toggleMode } = useThemeMode();
  const { t } = useI18n();
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const [mobileOpen, setMobileOpen] = useState(false);
  const { logout, user } = useAuthStore();
  const navigate = useNavigate();

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  return (
    <SidebarContext.Provider value={{ collapsed: sidebarCollapsed }}>
      <div className="relative min-h-screen bg-[var(--color-surface-base)] text-[var(--color-text-primary)]">
        <style>{`
          @media (min-width: 1024px) {
            .sidebar-transition {
              transition: width 0.3s ease-in-out;
            }
            .sidebar-collapsed {
              width: 64px !important;
            }
            .content-wrapper {
              margin-left: 260px;
            }
            .content-wrapper.content-collapsed {
              margin-left: 64px !important;
            }
            .content-wrapper .max-width-wrapper {
              max-width: 72rem;
              margin: 0 auto;
            }
            .content-wrapper.content-collapsed .max-width-wrapper {
              max-width: none !important;
            }
          }
        `}</style>
      <a
        href="#main-content"
        className="sr-only focus:not-sr-only focus:absolute focus:top-4 focus:left-4 focus:z-50 rounded-[var(--radius-md)] bg-[var(--color-semantic-primary-500)] px-3 py-2 text-sm font-medium text-[var(--color-semantic-primary-contrast)] shadow-lg"
      >
        B qua ti ni dung chnh
      </a>

      {mobileOpen && (
        <button
          type="button"
          aria-label="ng menu"
          className="fixed inset-0 z-40 bg-black/40 lg:hidden"
          onClick={() => setMobileOpen(false)}
        />
      )}

      <aside
        className={cn(
          'z-50 flex h-full flex-col border-r border-[var(--color-border-subtle)] bg-[var(--color-surface-card)] sidebar-transition',
          'lg:fixed lg:top-0 lg:left-0 lg:h-screen lg:translate-x-0',
          mobileOpen ? 'fixed inset-y-0 left-0 w-64 translate-x-0 shadow-xl p-6' : '-translate-x-full lg:flex p-6',
          sidebarCollapsed ? 'lg:p-2 sidebar-collapsed' : '',
        )}
        aria-label="iu hng ng dng"
      >
        <div className="flex items-center justify-between gap-2 mb-6">
          {!sidebarCollapsed && <span className="text-lg font-semibold tracking-tight">ZenaManage</span>}
          <button
            onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
            className="flex items-center justify-center w-8 h-8 rounded-md hover:bg-[var(--color-surface-muted)] text-[var(--color-text-secondary)] hover:text-[var(--color-text-primary)] transition-colors"
            aria-label={sidebarCollapsed ? 'Expand sidebar' : 'Collapse sidebar'}
            type="button"
          >
            {sidebarCollapsed ? <ChevronRight className="h-5 w-5" /> : <ChevronLeft className="h-5 w-5" />}
          </button>
        </div>
        <nav className="flex flex-col gap-2 text-sm font-medium">
          {navItems.map((item) => (
            <NavLink
              key={item.to}
              to={item.to}
              className={({ isActive }) =>
                cn(
                  'rounded-[var(--radius-md)] px-4 py-3 transition-colors',
                  isActive
                    ? 'bg-[var(--color-semantic-primary-50)] text-[var(--color-semantic-primary-700)]'
                    : 'text-[var(--color-text-secondary)] hover:bg-[var(--color-surface-muted)] hover:text-[var(--color-text-primary)]',
                  sidebarCollapsed && 'px-2 justify-center',
                )
              }
              onClick={() => setMobileOpen(false)}
            >
              {!sidebarCollapsed ? (
                <>
                  <span className="block">{item.label}</span>
                  <span className="text-xs font-normal text-[var(--color-text-muted)]">{item.description}</span>
                </>
              ) : (
                <span className="text-xs">{item.label.charAt(0)}</span>
              )}
            </NavLink>
          ))}
        </nav>
        <Button variant="ghost" size="sm" className="mt-auto lg:hidden" onClick={() => setMobileOpen(false)}>
          ng
        </Button>
      </aside>

      <div className={cn('flex min-h-screen flex-col content-wrapper', sidebarCollapsed && 'lg:content-collapsed full-width')}>
        <header className="border-b border-[var(--color-border-subtle)] bg-[var(--color-surface-card)]">
          <div className={cn('flex items-center justify-between gap-4 py-4', sidebarCollapsed ? 'px-1' : 'px-4 lg:px-8')}>
            <div className="flex items-center gap-3">
              <h1 className="text-base font-semibold leading-tight">ZenaManage</h1>
            </div>
            <div className="flex items-center gap-4 flex-1 justify-end">
              <span className="text-sm text-[var(--color-text-secondary)] hidden md:block">
                Xin cho, <span className="font-medium text-[var(--color-text-primary)]">{user?.name || 'User'}</span>
              </span>
              <Button variant="ghost" size="sm" aria-label={t('common.toggleTheme', { defaultValue: 'Toggle theme' })} onClick={toggleMode}>
                {mode === 'light' ? t('common.darkMode', { defaultValue: 'Dark mode' }) : t('common.lightMode', { defaultValue: 'Light mode' })}
              </Button>
              <Button variant="outline" size="sm" onClick={handleLogout}>
                Logout
              </Button>
            </div>
          </div>
        </header>

        <main id="main-content" className={cn('flex-1 overflow-y-auto py-8', sidebarCollapsed ? 'px-1' : 'px-4 lg:px-8')}>
          <div className={cn('flex w-full flex-col gap-6 max-width-wrapper')}>
            <Outlet />
          </div>
        </main>
      </div>
      </div>
    </SidebarContext.Provider>
  );
};

export default MainLayout;
