import { STORAGE_KEYS } from '../constants'
import { User } from '../types'

/**
 * Utilities cho authentication v authorization
 */

/**
 * Lu JWT token vo localStorage
 */
export const setToken = (token: string): void => {
  localStorage.setItem(STORAGE_KEYS.AUTH_TOKEN, token)
}

/**
 * Ly JWT token t localStorage
 */
export const getToken = (): string | null => {
  return localStorage.getItem(STORAGE_KEYS.AUTH_TOKEN)
}

/**
 * Xa JWT token khi localStorage
 */
export const removeToken = (): void => {
  localStorage.removeItem(STORAGE_KEYS.AUTH_TOKEN)
  localStorage.removeItem(STORAGE_KEYS.USER_PROFILE)
}

/**
 * Kim tra xem user  ng nhp cha
 */
export const isAuthenticated = (): boolean => {
  const token = getToken()
  return !!token && !isTokenExpired(token)
}

/**
 * Kim tra token c ht hn khng
 */
export const isTokenExpired = (token: string): boolean => {
  try {
    const payload = JSON.parse(atob(token.split('.')[1]))
    const currentTime = Date.now() / 1000
    return payload.exp < currentTime
  } catch {
    return true
  }
}

/**
 * Lu thng tin user vo localStorage
 */
export const setUserProfile = (user: User): void => {
  localStorage.setItem(STORAGE_KEYS.USER_PROFILE, JSON.stringify(user))
}

/**
 * Ly thng tin user t localStorage
 */
export const getUserProfile = (): User | null => {
  const userStr = localStorage.getItem(STORAGE_KEYS.USER_PROFILE)
  if (!userStr) return null
  
  try {
    return JSON.parse(userStr)
  } catch {
    return null
  }
}

/**
 * Kim tra user c permission khng
 */
export const hasPermission = (permission: string, user?: User | null): boolean => {
  if (!user) {
    user = getUserProfile()
  }
  
  if (!user) return false
  
  return user.permissions.includes(permission)
}

/**
 * Kim tra user c role khng
 */
export const hasRole = (roleName: string, user?: User | null): boolean => {
  if (!user) {
    user = getUserProfile()
  }
  
  if (!user) return false
  
  return user.roles.some(role => role.name === roleName)
}

/**
 * Kim tra user c phi admin khng
 */
export const isAdmin = (user?: User): boolean => {
  return hasRole('admin', user) || hasRole('super_admin', user)
}

/**
 * Kim tra user c quyn truy cp project khng
 */
export const canAccessProject = (_projectId: string, user?: User | null): boolean => {
  if (!user) {
    user = getUserProfile()
  }
  
  if (!user) return false
  
  // Admin c th truy cp tt c projects
  if (isAdmin(user)) return true
  
  // Kim tra permission c th cho project
  return hasPermission('project.read', user)
}